body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --main-blue: #0069ed;
  --main-pink: #f87ebe;
  --main-green: #009e51;
  --main-yellow: #f6c416;
  --main-white: #ffffff;
  --main-darkGray: #313131;

  --main-pale-blue: rgba(0, 107, 237, 0.15);
  --main-pale-pink: rgba(248, 126, 189, 0.15);
  --main-pale-green: rgb(0, 158, 81, 0.15);
  --main-pale-yellow: rgb(246, 196, 22, 0.15);

  --main-input-color: #efefef;
  --main-disabled-color: #e9e9e9;
  --main-disabled2-color: #c0c0c0;

  --main-font: "Jalnan", sans-serif;
  --sub-font: "Roboto", sans-serif;
  --main-font-size: 16px;
}
